<template>
  <div>
    <h1 style="text-align: center;">视频聊天室</h1>
    <h3>输入同样的房间名称即可发起聊天</h3>
    <div class="row1">
      <input :readonly="roomReadonly" type="text" v-model="roomid" placeholder="输入房间名称" id="roomid" />
      <button id="connect" class="btn" @click="joinRoom">加入</button>
      <button id="disconnect" class="btn" @click="closeWS">离开/挂断</button>
    </div>
    <div class="row2">
      <button id="getAudio" class="btn launchBtn" @click="launchChat('audio')">发起语音聊天</button>
      <button id="getVideo" class="btn launchBtn" @click="launchChat('video')">发起视频聊天</button>
      <button id="getScreen" class="btn launchBtn" @click="launchChat('screenshare')">发起屏幕共享</button>
    </div>
    <textarea readonly v-model="logVal" id="logs"></textarea>
    <!-- 视频播放 -->
    <div class="videobox">
      <div class="localbox" style="margin-bottom: 20px;">
        <div class="video-wrap">
          <video ref="localVideo" muted src id="localVideo"></video>
        </div>
      </div>
      <div class="remotebox">
        <div class="video-wrap">
          <video ref="remoteVideo" src id="remoteVideo"></video>
        </div>
      </div>
    </div>
    <div class="audiobox">
      <audio src="" controls></audio>
    </div>
    <div class="screenshare">
      <video ref="remoteVideo" src id="screenVideo" controls></video>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      roomid: '',
      logVal: '',
      user: '',
      subType: '',
      isWsConnect: false,
      isChat: false,
      roomReadonly: false,
      ws: '',
      serverConfig: {
        iceServers: [{ urls: ['stun:stun.xten.com'] }]
      },
      audio: '',
      localVideo: '',
      localVideoShow: false,
      remoteVideo: '',
      remoteVideoShow: false,
      localAudio: '',
      remoteAudioShow: false,
      screenVideo: '',
      localStream: '',
      remoteStream: '',
      rtcPC: '',
      btnEls: {}
    }
  },
  watch: {
    isWsConnect(val, oldVal) {
      if (val) {
        this.roomReadonly = true
        this.btnEls.connect.disabled = true
        this.btnEls.disconnect.disabled = false
        // if (!this.isChat) {
        //   this.btnEls.connect.getAudio = false
        //   this.btnEls.connect.getVideo = false
        //   this.btnEls.connect.getScreen = false
        // }
      } else {
        this.roomReadonly = false
        // this.btnEls.connect.disabled = false
        // this.btnEls.disconnect.disabled = true
        // this.btnEls.connect.getAudio = true
        // this.btnEls.connect.getVideo = true
        // this.btnEls.connect.getScreen = true
      }
    },
    isChat(val) {
      if (val) {
        this.btnEls.connect.getAudio = true
        this.btnEls.connect.getVideo = true
        this.btnEls.connect.getScreen = true
      }
    }
  },
  mounted() {
    this.initRtcPC()
    this.localVideo = document.getElementById('localVideo')
    this.remoteVideo = document.getElementById('remoteVideo')
    this.screenVideo = document.getElementById('screenVideo')
    this.audio = document.querySelector('audio')
    this.remoteVideo.onloadeddata = () => { this.remoteVideo.play() }
    this.localVideo.onloadeddata = () => { this.localVideo.play() }
    this.screenVideo.onloadeddata = () => { this.screenVideo.play() }
    this.btnEls = {
      getAudio: document.getElementById('getAudio'),
      getVideo: document.getElementById('getVideo'),
      getScreen: document.getElementById('getScreen'),
      connect: document.getElementById('connect'),
      disconnect: document.getElementById('disconnect'),
    }
  },
  methods: {
    // 初始化rtc
    initRtcPC() {
      console.log('init rtc')
      this.closeRtcPC()
      this.rtcPC = new RTCPeerConnection(this.serverConfig)
      this.rtcPC.addEventListener('icecandidate', e => this.onIceCandidate(e))
      this.rtcPC.addEventListener('iceconnectionstatechange', e => this.onIceStateChange(this.rtcPC, e))
      this.rtcPC.onaddstream = function(e) { console.log('addStream') }
      // 监听流加入音视频轨道
      var that = this
      this.rtcPC.ontrack = function(e) {
        console.log('ontrack')
        that.remoteStream = e.streams
        if (that.subType === 'audio') {
          if (that.audio.srcObject != e.streams[0]) {
            that.audio.srcObject = e.streams[0]
            that.audio.pause()
            that.audio.play()
          }
        } else if (that.subType === 'video') {
          if (that.remoteVideo.srcObject != e.streams[0]) {
            that.remoteVideo.srcObject = e.streams[0]
          }
        } else if (that.subType === 'screenshare') {
          if (that.screenVideo.srcObject != e.streams[0]) {
            that.screenVideo.srcObject = e.streams[0]
          }
        }
        
      }
    },
    // closertc
    closeRtcPC() {
      if (this.rtcPC) {
        console.log('close rtc')
        this.rtcPC.getLocalStreams().forEach(stream => {
          stream.getTracks().forEach(track => track.stop())
          console.log('getLocalStreams and stop', stream)
          this.rtcPC.removeStream(stream)
        })
        this.rtcPC.getRemoteStreams().forEach(stream => {
          stream.getTracks().forEach(track => track.stop())
          console.log('getRemoteStreams stop', stream)
          this.rtcPC.removeStream(stream)
        })
        this.rtcPC.close()
        this.rtcPC = ''
      }
    },
    // 更新日志
    updateLog(msg, other) {
      this.logVal += other ? other : '' + ' ' + msg + '\n';
    },
    // 点击加入房间
    joinRoom() {
      if (this.roomid == '') {
        alert('不能为空')
        return
      }
      this.connectWs()
    },
    // 加入房间成功
    joinSuccess(res) {
      this.user = res.user
      this.isWsConnect = true
    },
    // 连接websocket
    connectWs() {
      this.ws = new WebSocket('wss://127.0.0.1:8001')
      this.ws.onopen = e => {
        this.wsSendMessage('join', '大家好')
      }
      this.ws.onclose = e => {
        this.updateLog('离开房间,连接关闭')
        this.isWsConnect = false
      }
      this.ws.onerror = e => {
        this.updateLog('连接出错', e)
        this.isWsConnect = false
      }
      this.ws.onmessage = e => {
        var res = ''
        try {
          res = JSON.parse(e.data)
        } catch {
          res = e.data
        }
        this.handleWsMessage(res)
      }
    },
    // 发送消息
    async wsSendMessage(type, msg) {
      msg && typeof msg == 'object' ? (msg = JSON.stringify(msg)) : ''
      if (type == 'invite') console.log('sendmessage inite msg', msg)
      await this.ws.send(JSON.stringify({ roomid: this.roomid, user: this.user, type, subType: this.subType, msg }))
    },
    // 处理ws返回的消息
    handleWsMessage(res) {
      switch (res.type) {
        case 'broadcast': this.updateLog(res.msg); break;
        // case 'videoInvite': this.handleVideoInvite(res.msg); break;
        case 'invite': this.handleInvite(res); break;
        case 'acceptInvite': this.acceptInvite(res); break;
        case 'refuseInvite': this.refuseInvite(res); break;
        case 'offer': this.recOffer(res); break;
        case 'answer': this.getResponse(res.msg); break;
        case 'candidate': this.addIceCandidate(res.msg); break;
        // case 'noOffer': this.openLocalDevice(); break;
        // case 'hasOffer': document.getElementById('getVideo').disabled = true; break;
        case 'joinSuccess': this.joinSuccess(res); break;
        case 'joinFail': this.isWsConnect = false;this.updateLog(res.msg); break;
        default: this.updateLog(res.msg, '对方');
      }
    },
    judgeState() {
      if (this.roomid === '') {
        alert('请先加入房间')
        return false
      }
      return true
    },
    // 向对方发出语音聊天邀请
    launchChat(subType) {
      if (!this.judgeState()) return
      this.subType = subType
      var msg = ''
      if (subType === 'audio') {
        this.updateLog('向对方发出语音聊天邀请')
        msg = '对方邀请您语音聊天'
      } else if (subType === 'video') {
        this.updateLog('向对方发出视频聊天邀请')
        msg = '对方邀请您视频聊天'
      } else if (subType === 'screenshare') {
        this.updateLog('向对方发出观看屏幕共享邀请')
        msg = '对方邀请您观看屏幕共享'
      }
      this.wsSendMessage('invite', msg)
    },
    // 处理对方发来的邀请
    handleInvite(res) {
      this.updateLog(res.msg)
      console.log('handleInvie, msg', res.msg)
      this.subType = res.subType
      if (window.confirm('是否接受' + res.msg)) {
        this.wsSendMessage('acceptInvite', '接受邀请')
        this.updateLog('接受对方邀请, 请允许打开相关设备')
      } else {
        this.wsSendMessage('refuseInvite', '拒绝邀请')
        this.updateLog('拒绝对方邀请')
      }
    },
    // 处理对方发来的视频邀请
    handleVideoInvite(msg) {
      // 可以播放一段铃声
      var btns = document.getElementsByClassName('launchBtn')
      for (let i = 0; i < btns.length; i++) {
        btns[i].disables = true
      }
      this.updateLog('对方发来视频聊天邀请')
      if (window.confirm('是否接受视频聊天邀请')) {
        this.initRtcPC()
        this.wsSendMessage('acceptInvite', '接受邀请')
        this.updateLog('接受视频邀请, 请允许打开摄像头和麦克风')
      } else {
        this.wsSendMessage('refuseInvite', '拒绝邀请')
        this.updateLog('拒绝视频邀请')
        for (let i = 0; i < btns.length; i++) {
          btns[i].disables = true
        }
      }
    },
    // 对方接受邀请
    acceptInvite(res) {
      this.updateLog('对方接受邀请')
      this.initRtcPC()
      this.openLocalDevice(res.subType, 'createOffer')
    },
    // 对方拒绝视频邀请
    refuseInvite(msg) {
      this.updateLog('对方拒绝邀请')
    },
    // 判断打开本地设备
    openLocalDevice(deviceType, cb, param) {
      console.log('openLocalDevice', deviceType)
      if (deviceType === 'audio') {
        this.updateLog('请允许网页打开本地麦克风')
        this.openAudioDevice(cb, param)
      } else if (deviceType === 'video') {
        this.updateLog('请允许网页打开本地摄像头和麦克风')
        this.openVideoDevice(cb, param)
      } else if (deviceType === 'screenshare') {
        if (cb === 'createAnswer') {
          this.createAnswer(param)
        } else {
          this.updateLog('请选择共享窗口')
          this.openDisplayDevice(cb, param)
        }
      }
    },
    // 打开本地麦克风
    openAudioDevice(cb, param) {
      navigator.getUserMedia({ audio: true }, stream => {
        console.log('获取到本地音频流')
        this.localStream = stream
        this.localStream.getTracks().forEach(track => this.rtcPC.addTrack(track, this.localStream))
        console.log('openAudioDevice, add track to pc')
        // createoffer
        if (cb === 'createOffer') {
          this.createOffer()
        } else if (cb === 'createAnswer') {
          this.createAnswer(param)
        }
      }, error => {
        console.log('opendevice error', error)
        this.wsSendMessage('other', error)
      })
    },
    // 打开本地摄像头和麦克风
    openVideoDevice(cb, param) {
      navigator.getUserMedia({ video: true, audio: true }, stream => {
        console.log('获取到本地视频流')
        this.localStream = stream
        this.localVideo.srcObject = stream
        this.localStream.getTracks().forEach(track => this.rtcPC.addTrack(track, this.localStream))
        console.log('openVideoDevice, add track to pc')
        // createoffer
        if (cb === 'createOffer') {
          this.createOffer()
        } else if (cb === 'createAnswer') {
          this.createAnswer(param)
        }
      }, error => {
        console.log('opendevice error', error)
        this.wsSendMessage('other', error)
      })
    },
    // 选择共享窗口
    async openDisplayDevice(cb, param) {
      try {
        var stream = await navigator.mediaDevices.getDisplayMedia({ video: true, audio: true })
        console.log('获取到本地共享窗口流')
        this.localStream = stream
        this.screenVideo.srcObject = stream
        this.localStream.getTracks().forEach(track => this.rtcPC.addTrack(track, this.localStream))
        console.log('openDisplayDevices, add track to pc')
        // createoffer
        if (cb === 'createOffer') {
          this.createOffer()
        } else if (cb === 'createAnswer') {
          this.createAnswer(param)
        }
      } catch (error) {
        console.log('opendevice error', error)
        this.wsSendMessage('other', error)
      }
    },
    // 1.创建发起offer
    createOffer() {
      this.rtcPC.createOffer(offer => {
        this.rtcPC.setLocalDescription(new RTCSessionDescription(offer), () => {
          console.log('createoffer')
          this.wsSendMessage('offer', offer)
        }, error => { console.log('setLocalDescription error', error) })
      }, error => { console.log('createOffer error', error) })
    },
    // 2.创建回答
    createAnswer(offer) {
      this.rtcPC.setRemoteDescription(new RTCSessionDescription(offer), () => {
        this.rtcPC.createAnswer(answer => {
          this.rtcPC.setLocalDescription(new RTCSessionDescription(answer), () => {
            this.wsSendMessage('answer', answer)
          }, error => console.log('setLocalDescription', error))
        }, error => console.log('createAnswer', error))
      }, error => console.log('setRemoteDescription', error))
    },
    // 3.收到回复
    getResponse(answer) {
      console.log('recAnswer', typeof answer)
      answer = JSON.parse(answer)
      this.rtcPC.setRemoteDescription(new RTCSessionDescription(answer), () => {  }, error => {  })
    },
    // 接收到offer
    async recOffer(res) {
      console.log('recoffer', typeof res.msg)
      var offer
      if (typeof res.msg === 'object') {
        offer = res.msg
      } else {
        offer = JSON.parse(res.msg)
      }
      this.openLocalDevice(res.subType, 'createAnswer', offer)
    },
    // 获取到本地IceCandidate
    async onIceCandidate(event) {
      console.log('onIceCandidate')
      try {
        if (event.candidate) {
          this.wsSendMessage('candidate', event.candidate)
        }
      } catch (e) {
        console.log('onAddIceCandidateError', e)
      }
    },
    // 添加对方IceCandidate
    async addIceCandidate(candidate) {
      var rtcIceCandidate
      try {
        candidate = JSON.parse(candidate)
        rtcIceCandidate = new RTCIceCandidate(candidate)
        await this.rtcPC.addIceCandidate(rtcIceCandidate)
      } catch {}
      console.log('reciver candidate and add address',rtcIceCandidate.address)
      this.updateLog('对方address:' + rtcIceCandidate.address)
    },
    onIceStateChange(pc, event) {
      if (pc) {
        console.log(`${pc} ICE state: ${pc.iceConnectionState}`)
        console.log('ICE state change event: ', event)
        if (pc.iceConnectionState === 'disconnected') {
          this.closeRtcPC()
        }
      }
    },
    // 关闭websocket连接
    closeWS() {
      this.rtcPC && this.closeRtcPC()
      if (this.ws) {
        this.wsSendMessage('leave', 'leave room')
        this.wsSendMessage('leave', '对方挂断了电话')
        this.ws.close()
        this.ws = ''
        this.isWsConnect = false
      }
    }
  },
  beforeDestroy() {
    this.closeWS()
  }
}
</script>

<style>
.videobox {
  width: 100%;
  margin-top: 100px;
  /* display: flex; */
  /* justify-content: space-around; */
}
.localbox, .remotebox {
  width: 40%;
  /* height: 300px; */
}
.videobox .video-wrap {
  background: #000;
  height: 100%;
}
.videobox video {
  /* display: none; */
  /* width: 40%; */
}
textarea {
  margin-top: 30px;
  width: 40%;
  height: 200px;
}
textarea:focus {
  outline: none;
}
textarea:hover {
  outline: none;
}
.row1 {
  margin-bottom: 20px;
}
.btn {
  line-height: 1;
  white-space: nowrap;
  cursor: pointer;
  background: #fff;
  border: 1px solid #dcdfe6;
  color: #606266;
  -webkit-appearance: none;
  text-align: center;
  box-sizing: border-box;
  outline: none;
  margin: 0 20px;
  transition: .1s;
  font-weight: 500;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  padding: 12px 20px;
  font-size: 14px;
  border-radius: 4px;
}
.btn:hover {
  color: #409eff;
    border-color: #c6e2ff;
    background-color: #ecf5ff;
}
#roomid {
  background-color: #fff;
  background-image: none;
  border-radius: 4px;
  border: 1px solid #dcdfe6;
  box-sizing: border-box;
  color: #606266;
  display: inline-block;
  font-size: inherit;
  height: 40px;
  line-height: 40px;
  outline: none;
  padding: 0 15px;
  transition: border-color .2s cubic-bezier(.645,.045,.355,1);
  width: 200px;
}
</style>
