import Vue from 'vue'
import VueRouter from 'vue-router'
// import Home from '../views/Home.vue'
import Chat from '../views/videochat.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Chat',
    component: Chat
  },
  {
    path: '*',
    redirect: '/'
  }
]

const router = new VueRouter({
  baseUrl: 'videochat',
  routes
})

export default router
